/* New Button Style */
a:hover {
  color: inherit;
  text-decoration: none;
  border-radius: 30px;
}

.butn {
  padding: 12px 35px;
  background: #fff;
  border-radius: 30px;
  border: 1px solid transparent;
  position: relative;
  z-index: 3;
  margin-left: 10px;
  margin-right: 10px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
  cursor: pointer;
  outline: none !important;
  /* overflow: hidden; */
}
.butn span {
  position: relative;
  z-index: 2;
  border-radius: 30px;
}
.butn:after {
  border-radius: 30px;

  content: "";
  width: 0;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  transition: width 0.4s;
  z-index: 1;
  opacity: 1;
}
.butn:hover:after {
  border-radius: 30px;
  width: 100%;
  border-radius: 30px;
}

.butn-bg {
  background: #1a237e;
  border-color: #1a237e;
  color: #fff;
  border-radius: 30px;
}
.butn-bg:hover span {
  color: #1a237e;
  border-radius: 30px;
}

.butn-light,
.butn-bord {
  background: #fff;
  border-radius: 30px;
}
.butn-light:after,
.butn-bord:after {
  background: #1a237e;
  border-radius: 30px;
}
.butn-light:hover span,
.butn-bord:hover span {
  color: #fff !important;
  border-radius: 30px;
}
.butn-light span,
.butn-bord span {
  color: #1a237e;
  border-radius: 30px;
}

.butn-bord {
  background: transparent !important;
  border-color: #1a237e;
  border-radius: 30px;
}
.header .caption .butn {
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s;
}

/* ----------------------------------------------------------------
       [ 06 Process ]
  -----------------------------------------------------------------*/
#Process {
  background-color: #f8f9fa;
  padding: 6rem 0;
}
.process .item {
  position: relative;
  z-index: 4;
}
.process .item img {
  position: absolute;
  width: 70%;
  right: -40%;
  top: 15px;
  opacity: 0.5;
}
.process .item img.tobotm {
  transform: rotateX(180deg);
}
.process .item.odd {
  margin-top: 50px;
}
.process .item .cont {
  padding: 50px 30px;
  background-color: #fff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  position: relative;
  z-index: 3;
  transition: all 0.3s;
}
.process .item .cont:hover {
  background-color: #1a237e;
}
.process .item .cont:hover h6 {
  color: #fff;
}
.process .item .cont:hover p {
  color: #eee;
}
.process .item .icon {
  color: #2576fd;
  font-size: 30px;
  width: 80px;
  height: 80px;
  line-height: 85px;
  border-radius: 50%;
  border: 1px dashed #eee;
  margin-bottom: 30px;
}
.process .item h3 {
  position: absolute;
  top: 25px;
  left: 0;
  width: 100%;
  color: #777;
  font-size: 50px;
  font-weight: bold;
  opacity: 0.1;
  z-index: -1;
}
.process .item h6 {
  color: #222;
  font-size: 16px;
  margin-bottom: 15px;
}
.process .item p {
  font-size: 14px;
  color: #999;
}

@media screen and (max-width: 991px) {
  .process .item img {
    display: none;
  }
}
/* ----------------------------------------------------------------
       [ End Process ]
  -----------------------------------------------------------------*/

#mainNav {
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #212529;
}
#mainNav .navbar-toggler {
  padding: 0.75rem;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 0.75rem;
  color: #fff;
  text-transform: uppercase;
  background-color: #fed136;
  border: 0;
}
#mainNav .navbar-brand {
  color: #fed136;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 700;
  letter-spacing: 0.0625em;
  text-transform: uppercase;
}
#mainNav .navbar-brand img {
  height: 1.5rem;
}
#mainNav .navbar-nav .nav-item .nav-link {
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 0.95rem;
  color: #fff;
  letter-spacing: 0.0625em;
}
#mainNav .navbar-nav .nav-item .nav-link.active,
#mainNav .navbar-nav .nav-item .nav-link:hover {
  color: #fed136;
}

@media (min-width: 992px) {
  #mainNav {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    border: none;
    background-color: transparent;
    transition: padding-top 0.3s ease-in-out, padding-bottom 0.3s ease-in-out;
  }
  #mainNav .navbar-brand {
    font-size: 1.5em;
    transition: font-size 0.3s ease-in-out;
  }
  #mainNav .navbar-brand img {
    height: 2rem;
    transition: height 0.3s ease-in-out;
  }
  #mainNav.navbar-shrink {
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: #212529;
  }
  #mainNav.navbar-shrink .navbar-brand {
    font-size: 1.25em;
  }
  #mainNav.navbar-shrink .navbar-brand svg,
  #mainNav.navbar-shrink .navbar-brand img {
    height: 1.5rem;
  }
  #mainNav .navbar-nav .nav-item {
    margin-right: 1rem;
  }
  #mainNav .navbar-nav .nav-item:last-child {
    margin-right: 0;
  }
}
header.masthead {
  padding-top: 10.5rem;
  padding-bottom: 6rem;
  text-align: center;
  color: #fff;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: cover;
}

@media (min-width: 768px) {
  header.masthead {
    padding-top: 17rem;
    padding-bottom: 12.5rem;
  }
}

@-webkit-keyframes imageAnimation {
  0% {
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  8% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes imageAnimation {
  0% {
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  8% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.zoomcarousel {
  -webkit-animation: ease-in 6s linear infinite 0s;
          animation: ease-in 6s linear infinite 0s;
  background: no-repeat center center scroll;
  background-size: cover;
  -webkit-animation: thing 20s;
  animation: thing 20s;
}
@-webkit-keyframes thing {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(1.2, 1.2);
  }
}
@keyframes thing {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(1.2, 1.2);
  }
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 80%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}
@media (min-width: 768px) {
  .partners img {
    opacity: 0.5;
    transform: scale(0.8);
    transition: all 0.3s ease-in-out;
  }
  .partners img:hover {
    opacity: 1;
    transform: scale(1);
  }
}

